import React, {Component} from "react";
import PropTypes from 'prop-types';

const BuyMoreProductsComponent = (props) => {
    const onClick = (event) => {
        event.preventDefault();
        props.addButtonClicked(event);
    };

    return (
        <div className="g-white1-bg u-box-shadow u-spacing">
            <div className="u-position-relative">
                <div className="u-padding-left-l u-padding-right-l">
                    <table className="u-table-fixed">
                        <tbody>
                            <tr>
                                <td className="opl-checkout__icon__cell">
                                    <span aria-hidden="true" className={"g-icon g-icon--only g-icon--" + props.icon} />
                                </td>
                                <td className="u-padding-top-l u-padding-l u-padding-right-l">
                                    <span className="u-font-bold">{props.buyMoreLabel}</span>
                                </td>
                                <td className="opl-checkout__button__cell u-padding-top-l u-padding-l">
                                    <a href="#" className="opl-product-addlink u-right u-small-right" onClick={onClick}/>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    );
}

BuyMoreProductsComponent.propTypes = {
    buyMoreLabel: PropTypes.string,
    icon: PropTypes.string,
    addButtonClicked: PropTypes.func
};

export default BuyMoreProductsComponent;

